@use "global" as *;

.voice {
}

.voice__inner {
  position: relative;
}

.voice__inner::before {
  display: none;
  content: "";
  background: url(../images/common/icon-sakura.svg);
  width: rem(140);
  left: calc(50% - min(49vw, rem(620)));
  top: rem(-140);
  position: absolute;
  z-index: -1;
  background-size: cover;
  aspect-ratio: 90 / 95;
  @include mq("md") {
    display: block;
  }
}

.voice__inner::after {
  display: none;
  content: "";
  background: url(../images/common/icon-sakura.svg);
  right: calc(50% - min(48vw, rem(549)));
  bottom: rem(-70);
  position: absolute;
  z-index: -1;
  background-size: contain;
  aspect-ratio: 90 / 95;
  width: rem(140);
  @include mq("md") {
    display: block;
  }
}

.voice__list {
  margin-top: rem(44);
  @include mq("md") {
    grid-template-columns: repeat(2, 1fr);
    margin-top: rem(29);
  }
}

.voice__button {
  margin-top: rem(40);
  width: 100%;
  text-align: center;
  @include mq("md") {
    margin-top: rem(80);
  }
}
